.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: flex;
    /* inline-block; */
    color: #f2f2f2;
    padding-left: 10px;
    padding-top: 40px;
}

.nav {
  /* width: 100%; */
  /* height: 100%; */
  overflow: hidden;
  width: 100vw;
  /* align-items: baseline; */
  height: 200px;
  opacity: 1.0;
  background-image:url(/static/media/lemonBallSedum2.6f3ac43a.jpg);
  background-size: cover;
}

#logo { 
  float: left;
  margin: 40px;
  /* vertical-align: middle; */
}

.nav-link.active{
  color: #eb99ff;
}

.nav-link {

  /* display: flex; */
  float: right;
  color: #ffffff;
  background-color: Transparent !important;
  /* text-align: center; */
  padding: 20px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}



.nav-link:hover {
  background-color: none;
  /* color: #a1499c */
  color: #cccccc;

}

.nav-right {
  /* float: right; */
  position: absolute;
  right: 0;
  padding: 2.5%
}
